import emotion from "../../../common/chat/emotionList";
const emotionUrl = {};
emotion.forEach(t => {
  emotionUrl[t.title] = "https:" + t.url;
});
export default {
  props:{
    content: {
      type: [String, Object]
    }
  },
  data(){
	  return {
		  contentArr: []
	  }
  },
  watch:{
    content:{
      handler(t, e){
        t &&
          t !== e &&
          "string" == typeof t && 
          (this.contentArr = this.parseEmotion(t));
      },
      deep:true,
      immediate:true
    }
  },
  methods: {
    tapImage() {
      const t = this.content.uploadImgUrl;
      uni.previewImage({ urls: [t], current: t });
    },
    parseEmotion(t) {
      let e = t;
      if ("string" != typeof e || !e) return [];
      e = e.replace(/&lt;/g, "<").replace(/&gt;/g, ">");
      const n = [];
      for (; -1 !== e.indexOf("["); ) {
        const t = e.indexOf("["),
          o = e.indexOf("]", t + 1);
        if (-1 === o) break;
        n.push({ type: "text", content: e.substring(0, t) });
        const r = e.substring(t + 1, o),
          i = { type: "em", content: r };
        emotionUrl[r]
          ? (i.url = emotionUrl[r])
          : ((i.type = "text"), (i.content = `[${r}]`)),
          n.push(i),
          (e = e.substring(o + 1));
      }
      return n.push({ type: "text", content: e }), n;
    }
  }
}
